.fxPromo {
  padding: 0 15px;
  height: 48px;
  background-color: #efeff1;
  color: #4a4a4f;
  font-size: 13px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.fxPromo > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 auto;
}

.fxPromo > div > span {
  margin-left: 10px;
}

.fxPromo__logo {
  width: 24px;
}

.fxPromo--blue {
  background: -webkit-gradient(linear, left top, left bottom, from(#45a1ff), color-stop(94%, #00feff));
  background: -webkit-linear-gradient(top, #45a1ff 0%, #00feff 94%);
  background: -o-linear-gradient(top, #45a1ff 0%, #00feff 94%);
  background: linear-gradient(-180deg, #45a1ff 0%, #00feff 94%);
  color: #fff;
}

.fxPromo--pink {
  background: -webkit-gradient(linear, left top, left bottom, from(#ff9400), color-stop(94%, #ff1ad9));
  background: -webkit-linear-gradient(top, #ff9400 0%, #ff1ad9 94%);
  background: -o-linear-gradient(top, #ff9400 0%, #ff1ad9 94%);
  background: linear-gradient(-180deg, #ff9400 0%, #ff1ad9 94%);
  color: #fff;
}

.fxPromo--blue a {
  color: #fff;
  font-weight: bold;
}

.fxPromo--pink a {
  color: #fff;
  font-weight: bold;
}

.fxPromo--blue a:hover {
  color: #eee;
  font-weight: bold;
}

.fxPromo--pink a:hover {
  color: #eee;
  font-weight: bold;
}



